@import "../mixins";
.form-group {
  position: relative;
  margin: 0;
  margin-bottom: 30px;
  input {
    border: none;
    border-bottom: 1px solid #000000;
    padding: 0 10px;
    height: 47px;
    width: 100%;
    font-size: 18px;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #000;
    }
    &:focus {
      &::placeholder {
        color: rgba(#000, 0.3);
      }
    }
  }
}

.fg{
  position: relative;
  .nice-select{
    border: 1px solid #EAEAEA;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #FFFFFF;
    padding: .9375em;
    height: 4em;
    border-radius: .25em !important;
    font-size: 1em;
    width: 100%;
    &:after{
      display: none;
    }
    &:before{
      order: 10;
      content: '';
      width: 1.5em;
      height: 1.5em;
      flex-shrink: 0;
      margin-left: .5em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      transition: inherit;
    }
    &.open{
      &:before{
        transform: rotate(-180deg);
      }
    }
  }
  .current{
    letter-spacing: 0.04em;
    text-transform: uppercase;
    font-weight: 700;
    font-size: .875em;
  }
  .list{
    margin-top: .375em;
    box-shadow: none;
    border-radius: .25em;
    border: inherit;
    padding: .3125em 0;
  }
  .option{
    border: none !important;
    display: flex;
    align-items: center;
    padding: 1.07em;
    font-size: .875em;
    text-transform: uppercase;
    font-weight: 700;
    &:hover,
    &.focus,
    &.selected.focus{
      background-color: transparent;
      color: $primary;
    }
  }
  label{
    position: absolute;
    font-weight: 700;
    font-size: 10px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #808080;
    top: 1.5em;
    left: 1.5em;
    z-index: 2;
    pointer-events: none;
    &~.nice-select{
      .current{
        transform: translateY(.7em);
      }
    }
  }
}

.m-check,
.m-radio{
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
  label {
    display: flex;
    min-height: 1.25em;
    font-size: 1em;
    cursor: pointer;
    font-weight: 500;
    &:before {
      content: '';
      z-index: 1;
      background-color: #F1F1F1;
      box-shadow: inset 0 .0625em .125em rgba(0, 0, 0, 0.08);
      width: 1.25em;
      height: 1.25em;
      transition: background-color .100s, border-color .100s;
      flex-shrink: 0;
      margin-right: .625em;
      background-repeat: no-repeat;
      background-position: center;
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
  &._not-label{
    label{
      &:before{
        margin-right: 0;
      }
    }
  }
}

.m-check{
  input{
    &:checked+label {
      &::before {
        background-color: #fff;
        box-shadow: inset 0 0 0 .1875em rgba(196, 14, 56, 0.19), inset 0 .0625em .125em rgba(0, 0, 0, 0.08);
        background-image: url(../img/check.svg);
      }
    }
  }
  label{
    &:before{
      border-radius: .3125em;
      background-size: 1.125em auto;
    }
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: .8125em;
    margin: 0 .0625em;
    svg{
      width: 100%;
      height: auto;
    }
    path{
      fill: #E0E0E0;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: #FFB800;
      }
      &~.rating-input__item {
        path{
          fill: #FFB800;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"] .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"] .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"] .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"] .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"] .rating-input__item:nth-child(n+1) {
  path{
    fill: #FFB800;
  }
}
