@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._backdrop{
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.62);
}

.m-badge{
  border-radius: 2em;
  padding: 0 .625em;
  display: flex;
  align-items: center;
  height: 1.6875em;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  span{
    font-size: .6875em;
  }
  &._red{
    background-color: $primary;
  }
  &._orange{
    background: #E1861A;
  }
  &._blue{
    background: #4DB7F3;
  }
}
