@import "../mixins";
.catalog-poster{
  color: #FFFFFF;
  position: relative;
  overflow-x: hidden;
  .swiper{
    &-slide{
      padding: 11em 0 13.5em;
      background-size: cover;
      background-position: center;
    }
    &-button{
      position: static;
      margin: 0 .625em 0 0;
    }
  }
  &__info{
    max-width: 31em;
    @include grid100gap(1.5em);
  }
  &__nav{
    position: absolute;
    left: 0;
    bottom: 3em;
    width: 100%;
    .container{
      display: flex;
      align-items: center;
    }
  }
}

.catalog{
  padding: 4.75em 0 4.125em;
  &__inner{
    display: grid;
    grid-template-columns: 19.375em calc(100% - 19.375em - 1.25em);
    justify-content: space-between;
  }
  .breadcrumbs{
    padding-top: 0;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__sort{
    width: 19.375em;
    max-width: 100%;
    &._mob{
      display: none;
    }
  }
  &__filtered{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -.625em;
    &-item{
      margin: .625em .625em 0 0;
      height: 2em;
      padding: 0 .9375em;
      border-radius: 2em;
      background: #EAEAEA;
      display: flex;
      align-items: center;
      font-weight: 600;
      text-transform: uppercase;
      span{
        font-size: .75em;
      }
    }
  }
  &__show-filter{
    display: none;
  }
  &__grid{
    margin-top: 1.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: -1px;
      width: 1px;
      height: 100%;
      background-color: #fff;
      z-index: 2;
    }
    .product-card{
      @media screen and (min-width: 993px){
        border-left: 1px solid #ECECEC;
        border-bottom: 1px solid #ECECEC;
        &:nth-child(3n - 2){
          //border-left: none;
        }
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3){
          .product-card{
            &__badges{
              top: 0;
            }
          }
        }
      }
    }
  }
  &__bottom{
    margin-top: 2.5em;
    display: flex;
    justify-content: center;
  }
  &__more{
    padding: 0 1.875em;
  }
}

.catalog-banner{
  border-left: 1px solid #ECECEC;
  border-bottom: 1px solid #ECECEC;
  padding: 1.75em 0 1.75em 1.75em;
  grid-column: 2/4;
  grid-row: 2/3;
  .header-card{
    height: 100%;
    &__img{
      min-height: 100%;
    }
    &__content{
      width: 33.0625em;
      max-width: 100%;
      left: 50%;
      transform: translateX(-50%);
      padding-top: 3.75em;
      padding-bottom: 3.75em;
    }
    .display3{
      line-height: 150%;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
  &__btn{
    padding: 0 1.875em;
  }
}

.filter{
  padding: 1em 0 2em;
  background: #FFFFFF;
  box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: sticky;
  top: 1.5em;
  &__bg{
    display: none;
  }
  &__top{
    min-height: 2.0625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1em;
  }
  &__selected{
    flex-shrink: 0;
    @include center;
    min-width: 2.0625em;
    height: 2.0625em;
    padding: 0 .25em;
    color: #FFFFFF;
    border-radius: 10em;
    background-color: $primary;
  }
  &__groups{
    margin-top: 1.25em;
  }
  &__group{
    padding: 1.25em 1em;
    &._has-selected{
      .filter__group-clear{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &._active{
      .filter__toggle{
        background-image: url(../img/minus.svg);
      }
    }
    &:not(:first-child){
      border-top: 1px solid #F4F4F4;
    }
    &-clear{
      background-image: url(../img/close.svg);
      @include bgImgSize(.625em);
      width: 1.625em;
      height: 1em;
      border-radius: .1875em;
      flex-shrink: 0;
      margin-right: .625em;
      opacity: 0;
      pointer-events: none;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__hidden{
    padding: 1em 0 .5em;
    display: none;
  }
  &__action{
    display: flex;
    align-items: center;
    padding-left: .5em;
    flex-shrink: 0;
  }
  &__toggle{
    width: 1em;
    height: 1em;
    flex-shrink: 0;
    background-image: url(../img/plus.svg);
    @include bgImgSize(100%);
  }
  &__checks{
    @include grid100gap(.75em);
  }
  &__nav{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 1em;
  }
  &__confirm{
    width: 100%;
  }
  &__refresh{
    font-weight: 600;
    text-transform: uppercase;
    padding: 0 .5em;
    height: 1.125em;
    margin: 1.5em auto 0;
    border-radius: .1875em;
    span{
      font-size: .75em;
    }
    img{
      width: .625em;
      display: block;
      flex-shrink: 0;
      margin-right: .375em;
    }
  }
}

.product-card{
  padding: 1.75em;
  position: relative;
  &__img{
    padding-bottom: 167%;
    position: relative;
    img{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      max-width: 100%;
      max-height: 100%;
      transition: all .2s;
    }
    ._view2{
      opacity: 0;
    }
    &:hover{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__title{
    text-align: center;
    @include truncate(2);
    margin-top: .5em;
    min-height: 2.66em;
  }
  &__props{
    @include grid100gap(.625em);
    margin-top: 1em;
  }
  &__prop{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      content: '';
      display: block;
      flex-grow: 1;
      border: 1px dashed #DDDDDD;
      margin: 0 .6em;
    }
    &-title,
    &-desc{
      font-size: .8125em;
    }
    &-title{
      order: -1;
    }
    &-desc{
      font-weight: 600;
      flex-shrink: 0;
    }
  }
  &__nav{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: .625em;
    margin-top: 1.25em;
  }
  &__compare{
    grid-column: 1/-1;
    border: none;
    background-color: transparent !important;
    svg{
      fill: $primary;
      width: 1.125em;
      height: 1.125em;
      margin-right: .5em;
    }
    &:hover{
      color: $black;
      svg{
        fill: $black;
      }
    }
  }
  &__rating{
    padding: .3125em 0;
    @include center;
    margin-top: .3125em;
  }
  &__badges{
    z-index: 2;
    position: absolute;
    top: 1.75em;
    left: 1.75em;
    display: flex;
    flex-direction: column;
    align-items: center;
    .m-badge{
      margin: 0 auto .3125em 0;
    }
  }
}



@media screen and (max-width: 992px){
  .catalog-poster{
    .swiper-slide{
      padding: 1.875em 0;
      min-height: 28.125em;
    }
    &__info{
      text-align: center;
      margin: 0 auto;
      gap: .875em;
    }
    &__nav{
      display: none;
    }
  }

  .catalog{
    overflow-x: hidden;
    padding: 1.875em 0 2.875em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__show-filter{
      display: inline-flex;
      position: relative;
      width: 5.75em;
      height: 1.75em;
      padding: 0 1em 0 .375em;
      font-weight: 700;
      color: #fff;
      text-transform: uppercase;
      &:before{
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        background-image: url(../img/filter-rect.svg);
        @include bgImgSize(100%);
        width: 100%;
        height: 100%;
      }
      img{
        width: .9325em;
        margin-right: .375em;
        display: block;
        flex-shrink: 0;
        position: relative;
        z-index: 2;
      }
      span{
        font-size: .625em;
        position: relative;
        z-index: 2;
      }
    }
    .display1{
      line-height: 127%;
      display: inline;
    }
    .breadcrumbs{
      display: none;
    }
    &__nav{
      display: none;
    }
    &__sort{
      &._desktop{
        display: none;
      }
      &._mob{
        display: block;
        width: calc(100% - 1.875em);
        margin: 0 auto 1.5em;
      }
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - (.875em / 2)));
      gap: 2.5em .875em;
      margin-top: 1.25em;
      &:before{
        display: none;
      }
    }
    &__bottom{
      margin-top: 3em;
    }
  }

  .filter{
    border-radius: 0;
    transition: all .5s;
    transform: translateX(-200%);
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 20.625em;
    height: 100vh;
    overflow-y: auto;
    max-width: calc(100% - 2.5em);
    background: #FFFFFF;
    box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.15);
    z-index: 11;
    padding: 1.875em .625em 4em;
    &._open{
      transform: translateX(0);
      opacity: 1;
      &~.filter__bg{
        display: block;
      }
    }
  }

  .product-card{
    padding: 0;
    &__badges{
      top: 0;
      left: 0;
    }
    &__title{
      font-size: .875em;
      line-height: 143%;
      min-height: 2.86em;
    }
    &__rating{
      margin-top: 0;
    }
    &__nav{
      margin-top: 1.25em;
      grid-template-columns: calc(100% - 2.6875em - .625em) 2.6875em;
      justify-content: space-between;
    }
    &__fit,
    &__props{
      display: none;
    }
    &__compare{
      grid-column: unset;
      background: #EAEAEA !important;
      span{
        display: none;
      }
      svg{
        margin: 0;
        fill: $black;
      }
    }
  }

  .catalog-banner{
    display: none;
  }
}
