.main{
  position: relative;
  @media screen and (min-width: 993px){
    .swiper-slide{
      padding: 10.375em 0;
    }
    .catalog-poster__info{
      gap: .625em;
      margin: 0 6.875em;
    }
    &._center{
      .catalog-poster__info{
        margin: 0 auto;
        text-align: center;
      }
      .main__nav{
        justify-content: center;
      }
    }
    &._right{
      .catalog-poster__info{
        margin: 0 6.875em 0 auto;
        text-align: right;
      }
      .main__nav{
        justify-content: flex-end;
      }
    }
  }
  .swiper{
    &-button{
      margin: 0;
      background: rgba(255, 255, 255, 0.3);
      opacity: 1;
      &-prev{
        left: 4.375em;
      }
      &-next{
        right: 4.375em;
      }
      svg{
        stroke: #fff;
      }
    }
  }
  .catalog-poster__nav{
    bottom: 50%;
    transform: translateY(50%);
    z-index: 2;
    .container{
      justify-content: space-between;
    }
  }

  .text16{
    line-height: 167%;
  }
  &__nav{
    display: flex;
    margin-top: .625em;
  }
  &__btn{
    font-weight: 700;
    padding: 0 1.875em;
  }
}


@media screen and (max-width: 992px){
  .main{
    .swiper-slide{
      min-height: 35.5em;
    }
    .catalog-poster__info{
      gap: 1em;
    }
    .display1{
      max-width: calc(100% - 2.1em);
      margin: 0 auto;
    }
    &__nav{
      margin-top: .25em;
      justify-content: center;
    }
  }
}
