.display1{
  font-size: 3.375em;
  line-height: 119%;
  letter-spacing: 0.02em;
  font-weight: 700;
}

.display2{
  font-weight: 700;
  font-size: 2.0625em;
  line-height: 129%;
  letter-spacing: 0.02em;
}

.display3{
  font-size: 1.5em;
  line-height: 100%;
  font-weight: 700;
  letter-spacing: 0.02em;
}

.display4{
  font-weight: 600;
  font-size: 1.25em;
  line-height: 120%;
  letter-spacing: 0.02em;
}

.text32{
  font-weight: 600;
  font-size: 2em;
  line-height: 131%;
  letter-spacing: 0.02em;
}

.text20{
  font-size: 1.25em;
  line-height: 180%;
  letter-spacing: 0.02em;
}

.text18{
  font-size: 1.125em;
  line-height: 133%;
}

.text16{
  font-size: 1em;
  line-height: 130%;
}

.text14{
  font-size: .875em;
  line-height: 200%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.accent-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 1.875em;
    line-height: 145%;
  }
  .display1{
    font-size: 1.875em;
    line-height: 127%;
  }

  .text32{
    font-size: 1em;
    line-height: 140%;
  }
}
