@import "../mixins";
.footer{
  padding: 4.375em 0 3.125em;
  background-color: $black;
  &__top{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1em));
    justify-content: space-between;
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-top: 1.875em;
  }
  &__social{
    background: #333333;
    width: 2.5em;
    height: 2.5em;
    @include center;
    border-radius: 50%;
    margin-right: .625em;
    img{
      width: 1.25em;
      transition: all .3s;
    }
    &:hover{
      transform: scale(1.1);
    }
  }
  &__contacts{
    @include grid100gap(.9375em);
    margin-top: 1.875em;
    &-group{
      display: flex;
    }
    &-icon{
      width: .875em;
      flex-shrink: 0;
      margin-right: .375em;
      img{
        width: 100%;
        display: block;
      }
    }
    &-content{
      @include grid100gap(.3125em);
      color: #7D7D7D;
      font-weight: 600;
    }
    &-phone,
    &-address{
      font-size: .75em;
      line-height: 125%;
    }
    &-phone{
      color: #B6B6B6;
    }
  }
  &__col{
    .display3{
      color: #FFFFFF;
    }
  }
  &__links{
    margin-top: 1.75em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link{
    margin-right: auto;
    font-weight: 500;
    font-size: .875em;
    line-height: 240%;
    letter-spacing: 0.02em;
    color: #B6B6B6;
  }
  &__bottom{
    margin-top: 3.75em;
    display: flex;
    justify-content: space-between;
  }
  &__text{
    font-size: .75em;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: #8F8F8F;
  }
  &__left{
    max-width: 16.875em;
    @include grid100gap(.625em);
  }
  &__center{
    max-width: 35.8125em;
  }
  &__map{
    color: #B6B6B6;
  }
  &__by{
    display: flex;
    align-items: flex-end;
    img{
      width: 9.625em;
      display: block;
      flex-shrink: 0;
      margin-left: .375em;
    }
    .footer__text{
      padding-bottom: .4em;
      color: #B6B6B6;
    }
  }
  &__catalog{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.375em;
  }
}

.footer-link{
  display: flex;
  align-items: center;
  color: #B6B6B6;
  font-weight: 600;
  text-transform: uppercase;
  svg{
    width: 2.125em;
    height: 2.125em;
    margin-right: .3125em;
    fill: #7A7A7A;
  }
  span{
    font-size: .6875em;
    line-height: 136%;
  }
  &:hover{
    color: #FFFFFF;
    svg{
      fill: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3.125em 0;
    &__catalog{
      display: none;
    }
    &__top{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1.75em 0;
    }
    &__col{
      &:nth-child(1){
        grid-column: 1/-1;
        margin-bottom: 1.25em;
      }
      .display3{
        font-size: 1.125em;
      }
    }
    &__top2{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__socials{
      margin: 0;
    }
    &__social{
      margin: 0 0 0 .625em;
    }
    &__contacts{
      grid-template-columns: 1fr 1fr;
      margin-top: 3.125em;
      &-group{
        &:nth-child(2){
          grid-column: 1/-1;
          order: 2;
        }
      }
    }
    &__links{
      margin-top: .75em;
    }
    &__link{
      font-size: .8125em;
    }
    &__bottom{
      margin-top: 2.875em;
      display: block;
    }
    &__left{
      gap: .375em;
    }
    &__center{
      margin: 3.125em 0 .75em;
    }
  }
}
