@import "../vars";
@import "../mixins";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-red{
    padding: 0 1.5em;
    background-color: $primary;
    text-transform: uppercase;
    color: #FFFFFF;
    border-radius: .25em;
    span{
        letter-spacing: 0.04em;
        font-size: .875em;
    }
    &:hover{
        background-color: $primaryHover;
        color: #fff;
    }
}

.m-btn-big{
    height: 3.0625em;
}

.m-btn43{
    height: 2.6875em;
}

.swiper-button{
    width: 2.75em;
    height: 2.75em;
    font-size: 1em;
    @include center;
    background: #FFFFFF;
    border-radius: 50%;
    box-shadow: 0 .0625em .0625em rgba(0, 0, 0, 0.05), 0 .25em .25em rgba(0, 0, 0, 0.05);
    svg{
        width: .875em;
        height: .875em;
        fill: none;
        stroke: $primary;
    }
    &:hover{
        opacity: .9;
        box-shadow: none;
    }
}

.m-btn-red-light{
    background-color: #FEF4F7;
    color: $primary;
    &:hover{
        color: $primary;
        background-color: darken(#FEF4F7,10%);
    }
}

.m-btn-red-outline,
.m-btn-gray-outline{
    font-weight: 700;
    text-transform: uppercase;
    border-radius: .25em;
    span{
        letter-spacing: 0.04em;
        font-size: .8125em;
    }
}


.m-btn-red-outline{
    color: $primary;
    border: 2px solid $primary;
    svg{
        fill:$primary;
    }
    &:hover{
        background-color: $primary;
        color: #fff;
        svg{
            fill: #fff;
        }
    }
}

.m-btn-gray-outline{
    color: $black;
    border: 2px solid #D4D4D4;
    &:hover{
        background-color: #D4D4D4;
        color: $black;
    }
}

.m-btn-gray{
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(1.375em);
    border-radius: .25em;
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FFFFFF;
    span{
        font-size: .875em;
    }
    &:hover{
        background: rgba(255, 255, 255, 0.5);
        color: #FFFFFF;
    }
}

.m-btn-gray2{
    background: #EFEFEF;
    border-radius: .25em;
    font-weight: 700;
    text-transform: uppercase;
    span{
        font-size: .8125em;
        letter-spacing: 0.04em;
    }
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.text-btn{
    font-weight: 600;
    color: $primary;
    display: flex;
    align-items: center;
    span{
        font-size: .875em;
    }
    svg{
        fill: none;
        stroke: $black;
        width: 1.5em;
        height: 1.5em;
        margin-right: .3125em;
    }
}
