@import "../mixins";
.catalog-item{
  background: #FBFBFB;
  padding-bottom: 8em;
  &__inner{
    margin-top: 2.625em;
    display: grid;
    grid-template-columns: 66.1% calc(33.9% - 1.25em);
    justify-content: space-between;
  }
  &__buttons{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 56% auto;
    gap: .625em;
  }
  &__btn{
    width: 100%;
    height: 3.25em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
    svg{
      width: 1em;
      height: 1em;
      margin-right: .625em;
    }
  }
  &__buttons2{
    margin-top: 1.25em;
  }
  &__downloads{
    @include grid100gap(1.25em);
    margin-top: 1.25em;
  }
}

.gallery{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  grid-template-rows: 1fr 1fr;
  gap: 1.25em;
  &__img{
    display: block;
    @include imgCover(100%);
  }
  &__wrap{
    position: relative;
  }
}

.gallery-360{
  position: absolute;
  right: 0;
  bottom: 0;
  width: calc(50% - .625em);
  z-index: 2;
  &__hint{
    position: absolute;
    bottom: 1.875em;
    left: 50%;
    transform: translateX(-50%);
    max-width: 14.125em;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 600;
    text-transform: uppercase;
    color: $primary;
    text-align: center;
    img{
      width: 1.875em;
      display: block;
      margin-bottom: .625em;
    }
    span{
      font-size: .75em;
      letter-spacing: 0.04em;
    }
  }
  &__wrap{
    position: relative;
    padding-bottom: 100%;
  }
  .turntable{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    ul{
      width: 100%;
      height: 100%;
    }
    li{
      width: 100%;
      height: 100%;
    }
    img{
      height: 100%;
      object-fit: cover;
    }
  }
}

.color{
  &__title{
    display: flex;
    align-items: center;
    .semi-bold-text{
      margin-right: .875em;
    }
  }
  &__wrap{
    margin-top: .625em;
    display: flex;
    flex-wrap: wrap;
  }
  &__item{
    border: 2px solid #fff;
    width: 5.375em;
    height: 5.375em;
    background: #FFFFFF;
    box-shadow: 0 .0625em .125em rgba(0, 0, 0, 0.05), 0 .1875em .3125em rgba(0, 0, 0, 0.03);
    border-radius: .25em;
    padding: .5em;
    margin: .75em .75em 0 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &:hover,
    &._active{
      border-color: $primary;
    }
  }
}

.props{
  margin-top: 1.875em;
  .product-card__props{
    display: grid;
    margin-top: 1.25em;
  }
  .product-card__prop-title,
  .product-card__prop-desc{
    font-size: .875em;
  }
  &__rating{
    margin: 1.25em 0 0 -.625em;
    display: inline-flex;
    padding: .3125em .625em;
    background: #FFFFFF;
    border-radius: 3.125em;
  }
}

.type{
  margin-top: 1em;
  display: flex;
  justify-content: space-between;
  &__info{
    width: 5em;
    height: 2.125em;
    background-image: url(../img/type.svg);
    @include bgImgSize(100%);
    margin-left: 1.5em;
    flex-shrink: 0;
    padding: 0 .5em;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-weight: 600;
    span{
      letter-spacing: 0.02em;
      font-size: 1.375em;
    }
  }
  &__text{
    font-weight: 500;
    font-size: .75em;
    line-height: 167%;
    letter-spacing: 0.02em;
  }
}

.collection{
  padding: 2.5em 0;
  overflow-x: hidden;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(4,25%);
    .product-card{
      &__rating{
        position: absolute;
        right: 1.75em;
        top: 1.75em;
        padding: .3125em .625em;
        background: #FFFFFF;
        border-radius: 3.125em;
      }
      @media screen and (min-width: 993px){
        border-left: 1px solid #ECECEC;
        &:nth-child(4n - 3){
          border-left: none;
        }
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4){
          padding-top: 0;
          .product-card{
            &__badges,
            &__rating{
              top: 0;
            }
          }
        }
      }
    }
  }
}

.detail{
  box-shadow: 0px -1px 0px #EBEBEB;
  padding-bottom: 5.625em;
  &__tabs{
    margin-top: -4.125em;
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: center;
  }
  &__content{
    padding-top: 3.75em;
    .display2{
      font-size: 2.75em;
    }
  }
}

.tab1{
  margin: 0 .25em;
  display: flex;
  align-items: center;
  height: 4.125em;
  background-color: #EBEBEB;
  border: 1px solid #EBEBEB;
  border-bottom: none;
  border-radius: .5em .5em 0 0;
  padding: 0 1.875em;
  font-weight: 600;
  font-size: 1em;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  transition: all .3s;
  svg{
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    margin-right: .75em;
    fill: none;
    stroke: $black;
  }
  &._active,
  &:hover{
    background-color: #fff;
    color: $primary;
    cursor: pointer;
    svg{
      stroke: $primary;
    }
  }
  &._active{
    pointer-events: none;
  }
}

._tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.advantages{
  .display2{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    width: 67.5em;
    max-width: 100%;
    margin: 2.75em auto 0;
    .advantage-card:nth-child(even){
      .advantage-card__right{
        order: -1;
      }
    }
  }
}

.advantage-card{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.875em));
  gap: 3.75em;
  align-items: center;
  &__img{
    @include imgCover(71.4%);
    img{
      border-radius: 1.25em;
    }
  }
  &__top{
    padding-bottom: 2.125em;
    margin-bottom: 1.875em;
    position: relative;
    width: calc(100% + 1em);
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 12.5em;
      height: .25em;
      background-color: $primary;
    }
  }
  &__bottom{
    max-width: 27.375em;
  }
}

.mode{
  margin-top: 5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__slider{
    margin-top: 2.75em;
    overflow-x: hidden;
  }
  .swiper{
    &-button{
      position: static;
      margin: 0 0 0 1.25em;
      border: 2px solid #E8E8E8;
      box-shadow: none;
    }
  }
}

.mode-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  img{
    height: 4.375em;
    width: auto;
    margin-bottom: 1em;
  }
  &__title{
    max-width: 11em;
  }
  .text14{
    font-weight: 600;
    line-height: 130%;
  }
}

.view{
  margin-top: 5.25em;
  min-height: 38.125em;
  background-size: cover;
  background-position: center;
  border-radius: .625em;
  display: block;
  padding: 3.125em 4.375em;
  color: #FFFFFF;
  &__text{
    max-width: 60.625em;
  }
  &__desc{
    font-size: 1.375em;
    line-height: 182%;
    letter-spacing: 0.02em;
  }
  &__items{
    margin-top: 1.25em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 2em);
  }
  &__item{
    margin-top: 1.875em;
    &:not(:last-child){
      margin-right: 3.125em;
    }
    .text16{
      font-weight: 500;
      line-height: 162%;
      letter-spacing: 0.02em;
    }
  }
  &__num{
    margin-bottom: .25em;
    span{
      font-weight: 700;
      font-size: 3.75em;
      line-height: 107%;
      letter-spacing: 0.02em;
    }
  }
}

.product-props{
  &__grid{
    margin-top: 2.875em;
    display: grid;
    grid-template-columns: 20em calc(100% - 20em);
  }
  &__left{
    padding-right: 2em;
  }
  &__nav{
    position: sticky;
    top: 1.5em;
    @include grid100gap(2em)
  }
  &__link{
    font-weight: 600;
    font-size: .875em;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    &._active{
      color: $primary;
    }
  }
}

.props-group{
  &:not(:last-child){
    margin-bottom: 4.375em;
  }
  &__title{
    font-weight: 700;
    font-size: 1.5625em;
    line-height: 136%;
    letter-spacing: 0.02em;
  }
  &__items{
    margin-top: 1.5em;
  }
  &__item{
    display: grid;
    grid-template-columns: 1fr 1.4fr;
    border-bottom: 1px solid #F4F4F4;
    padding: .5em 0;
    font-weight: 500;
    &-title,
    &-desc{
      font-size: .875em;
      line-height: 130%;
    }
    &-desc{
      font-weight: 600;
    }
  }
}

.support{
  &__grid{
    margin-top: 2.5em;
    display: flex;
    flex-wrap: wrap;
    .support-card{
      width: 22.5em;
      flex-shrink: 0;
    }
  }
}

.support-card{
  color: #FFFFFF;
  &__wrap{
    padding-bottom: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  &__content{
    @include centerAbsolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 12.5em;
    width: 100%;
    text-align: center;
    img{
      height: 3.75em;
      display: block;
      margin-bottom: .625em;
    }
  }
  &__title{
    font-size: 1.375em;
    line-height: 145%;
    letter-spacing: 0.02em;
    font-weight: 500;
    min-height: 2.9em;
  }
}

.faq{
  margin-top: 5em;
  &__items{
    margin-top: .625em;
  }
  .catalog__bottom{
    justify-content: unset;
    margin-top: 1.875em;
  }
}

.qa-card{
  padding: 1.5em 0;
  border-bottom: 1px solid #E6E6E6;
  &._active{
    .qa-card__btn{
      background-image: url(../img/minus2.svg);
    }
  }
  &._active,
  &__show:hover{
    .qa-card__title{
      color: $primary;
    }
    .qa-card__btn{
      background-color: $primary;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  &__btn{
    flex-shrink: 0;
    width: 2.75em;
    height: 2.75em;
    border-radius: 50%;
    background-color: $black;
    margin-right: 1.25em;
    background-image: url(../img/plus2.svg);
    @include bgImgSize(1em);
  }
  &__title{
    font-weight: 600;
    font-size: 1.25em;
    line-height: 130%;
    letter-spacing: 0.02em;
  }
  &__hidden{
    padding: 1em 0 .375em 4em;
    max-width: 74.375em;
    display: none;
  }
  &__text{
    line-height: 171%;
    letter-spacing: 0.02em;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    overflow-x: hidden;
    background-color: transparent;
    padding-bottom: 1.5em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.875em;
      margin-top: 1.5em;
    }
  }

  .collection{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 2.5em 1em;
    }
    .product-card{
      &:nth-child(2)~.product-card{
        display: none;
      }
      &__rating{
        right: 0;
        top: 0;
      }
    }
  }

  .detail{
    overflow-x: hidden;
    padding: 1.625em 0 0;
    &__tabs{
      margin-top: 0;
      flex-wrap: wrap;
      justify-content: unset;
      width: calc(100% + .625em);
    }
    &__content{
      padding-top: .625em;
      .display2{
        font-size: 1.875em;
      }
    }
  }

  .tab1{
    margin: 0 .625em .625em 0;
    padding: 0 .75em;
    height: 3em;
    border: none;
    border-radius: .625em;
    &:nth-child(2){
      order: 2;
      margin-right: 0;
    }
    span{
      font-size: .8125em;
    }
    svg{
      width: 1.25em;
      height: 1.25em;
    }
    &._active{
      background-color: $primary;
      color: #fff;
      svg{
        stroke: #fff;
      }
    }
  }

  .advantages{
    .display2{
      text-align: left;
    }
    &__grid{
      @include fullScroll;
      display: flex;
      gap: unset;
      max-width: unset;
      margin-top: 1.25em;
    }
  }

  .advantage-card{
    display: block;
    flex-shrink: 0;
    width: 18.75em;
    margin-right: .625em;
    &__img{
      padding-bottom: 80%;
      margin-bottom: 1em;
      img{
        border-radius: .625em;
      }
    }
    &__top{
      padding-bottom: 1em;
      margin-bottom: 1em;
      &:before{
        width: 7.125em;
      }
    }
    .text32{
      br{
        display: none;
      }
    }
    .text20{
      font-size: .8125em;
      line-height: 169%;
    }
  }

  .mode{
    margin-top: 3.125em;
    padding-bottom: 2.75em;
    position: relative;
    &__top{
      justify-content: center;
    }
    .display2{
      text-align: center;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
    &__slider{
      margin-top: 1.25em;
    }
    .swiper{
      &-button{
        margin: 0 .625em;
      }
    }
  }

  .mode-card{
    padding-bottom: 1.25em;
    .text14{
      font-size: .75em;
      line-height: 130%;
    }
    ._hide{
      display: none;
    }
  }

  .view{
    padding: 1.5em 1.875em 1.875em;
    min-height: unset;
    margin-top: 3.125em;
    &__desc{
      font-size: .75em;
    }
    &__item{
      &:not(:last-child){
        margin-right: 2em;
      }
      .text16{
        font-size: .75em;
      }
    }
    &__num{
      span{
        font-size: 2.5em;
      }
    }
  }

  .product-props{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    &__left{
      display: none;
    }
  }

  .props-group{
    &:not(:last-child){
      margin-bottom: 1.25em;
    }
    &__title{
      font-size: 1.25em;
      max-width: 70%;
    }
    &__items{
      margin-top: .5em;
    }
    &__item{
      grid-template-columns: 1fr 1fr;
      gap: .25em;
      padding: .625em 0;
      &-title,
      &-desc{
        font-size: .75em;
      }
    }
  }

  .support{
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
      margin-top: 1.25em;
      .support-card{
        width: 100%;
      }
    }
  }

  .faq{
    margin-top: 1.25em;
  }

  .qa-card{
    &__title{
      font-size: .875em;
      line-height: 143%;
      letter-spacing: 0.02em;
    }
    &__btn{
      margin-right: 1.125em;
    }
    &__hidden{
      padding: 1em 0 0 3.875em;
    }
    &__text{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    .display1{
      font-size: 1.25em;
    }
    &__buttons{
      grid-template-columns: 1fr 1fr;
      margin-top: 1.25em;
    }
    &__buttons2{
      grid-template-columns: 1.7fr 1fr;
    }
  }

  .gallery{
    overflow-x: auto;
    display: flex;
    gap: unset;
    width: calc(100% + 1.25em);
    margin-left: -.625em;
    &__item{
      margin-right: .0625em;
    }
  }

  .gallery__item{
    flex-shrink: 0;
    width: 18.125em;
  }

  .gallery-360{
    position: relative;
    width: 100%;
    margin-top: 1em;
  }

  .color{
    &__wrap{
      margin-top: 0;
      width: calc(100% + .625em);
    }
    &__item{
      margin: .625em .625em 0 0;
      padding: .375em;
      width: 4.75em;
      height: 4.75em;
    }
  }

  .type{
    margin-top: .875em;
    &__info{
      background-image: url(../img/type2.svg);
      width: 3.6875em;
      margin-left: .5em;
    }
  }

  .collection{
    padding: 3.375em 0;
  }

  .support-card{
    &__content{
      max-width: 7em;
      img{
        height: 1.875em;
        margin-bottom: .625em;
      }
    }
    &__title{
      font-size: .75em;
      line-height: 167%;
      min-height: 3.34em;
    }
  }
}
