@import "../mixins";
.logo{
  display: block;
  width: 8.5625em;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  &__logo{
    margin: 0 2.125em;
    &._mob{
      display: none;
    }
  }
}

.header-top{
  background-color: #fff;
  position: relative;
  &__inner{
    padding: .75em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header-search{
  flex-shrink: 0;
  position: relative;
  &__btn{
    width: 1.25em;
    height: 1.25em;
    &:hover{
      svg{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
      height: 100%;
      fill: $black;
    }
  }
  &__field{
    position: absolute;
    width: 26.25em;
    top: 100%;
    left: 0;
    margin-top: 2.8125em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    input{
      font-size: 1em;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      font-weight: 600;
      background: #F9F9F9;
      border: 1px solid #E2E2E2;
      border-radius: .625em;
      height: 4.25em;
      width: 100%;
      padding: 0 8em 0 1.875em;
      color: $black;
      &::placeholder{
        color: $black;
      }
      &:focus{
        border-color: $primary;
      }
    }
  }
  &__btn2{
    position: absolute;
    top: 50%;
    right: .9375em;
    transform: translateY(-50%);
  }
  &._active{
    .header-search__field{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.header-lang{
  flex-shrink: 0;
  padding: .25em;
  background: #F0F0F0;
  border-radius: 3.75em;
  display: flex;
  &__item{
    width: 3.4375em;
    height: 1.875em;
    border-radius: 5.875em;
    text-transform: uppercase;
    font-weight: 600;
    @include center;
    span{
      font-size: .75em;
      letter-spacing: 0.04em;
    }
    &:hover,
    &._active{
      color: $primary;
      background: #FFFFFF;
      box-shadow: 0 .0625em .0625em rgba(0, 0, 0, 0.05), 0 .25em .25em rgba(0, 0, 0, 0.05);
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  &__item{
    margin: 0 .75em;
    &._active{
      .submenu{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
      .menu__item-show{
        color: $primary;
      }
    }
  }
  &__item-show,
  &__link{
    font-size: .75em;
    line-height: 125%;
    font-weight: 600;
    text-transform: uppercase;
    &:hover{
      color: $primaryHover;
      cursor: pointer;
    }
  }
  &__bg{
    display: none;
  }
}

.submenu{
  @media screen and (min-width: 993px){
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 1.875em 0;
    max-height: calc(100vh - 10em);
    overflow-y: auto;
    background-color: #fff;
    z-index: 10;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__bg{
    display: none;
    z-index: 9;
    position: absolute;
    left: 0;
    top: 100%;
    height: 100vh;
    width: 100%;
    background: rgba(0, 0, 0, 0.3);
  }
  &__left{
    flex-grow: 1;
    padding: 2.5em 0;
  }
  &__right{
    width: 33.1875em;
    flex-shrink: 0;
    margin-left: 5em;
  }
  &__links{
    margin-top: 2.125em;
    width: 32.5em;
    max-width: 100%;
    column-count: 2;
    column-gap: 2.5em;
  }
  &__link{
    display: block;
    font-weight: 500;
    font-size: .875em;
    line-height: 140%;
    letter-spacing: 0.02em;
    margin-bottom: 2em;
    span{
      color: #AAAAAA;
    }
  }
  &__groups{
    margin-top: 1.625em;
    column-count: 2;
    column-gap: 2.5em;
  }
  &__services{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.25em 3.125em;
    margin-top: 2em;
    &-links{
      margin-top: 2em;
    }
  }
}

.submenu-group{
  max-width: 17.5em;
  margin-bottom: 1.25em;
  &__title{
    font-size: 1em;
    font-weight: 600;
    line-height: 240%;
    letter-spacing: 0.02em;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: calc(100% + 1.75em);
  }
  &__link{
    font-weight: 500;
    font-size: .875em;
    line-height: 200%;
    letter-spacing: 0.02em;
    &:not(:last-child){
      margin-right: 2em;
    }
  }
}

.header-card{
  display: block;
  position: relative;
  &__img{
    @include imgCover(62.5%);
    img{
      border-radius: 1.25em;
    }
  }
  &__content{
    color: #fff;
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    max-height: 100%;
    overflow-x: hidden;
    @include grid100gap(1.25em);
    text-align: center;
    padding: 2.5em 4.5em;
  }
}

.burger{
  display: none;
}

.header-bottom{
  background-color: $primary;
  &__inner{
    width: calc(100% + 3em);
    margin-left: -1.5em;
    display: flex;
    justify-content: space-between;
  }
}

.header-link{
  height: 3.625em;
  display: flex;
  align-items: center;
  padding: 0 1.25em;
  color: #fff;
  &:hover{
    background-color: $primaryHover;
    color: #fff;
  }
  img{
    width: 2.125em;
    display: block;
    flex-shrink: 0;
    margin-right: .3125em;
  }
  span{
    font-weight: 600;
    font-size: .6875em;
    line-height: 137%;
    text-transform: uppercase;
  }
}



@media screen and (max-width: 992px){
  .header{
    &__logo{
      &._mob{
        display: block;
      }
      &._desktop{
        order: -1;
        margin: 0;
      }
    }
    &._open{
      .menu{
        transform: translateX(0);
        opacity: 1;
        &__bg{
          display: block;
        }
      }
    }
  }

  .header-top{
    &__inner{
      padding: .75em 0;
      position: relative;
    }
    &__left{
      display: flex;
      align-items: center;
    }
  }

  .header-search{
    position: static;
    &__field{
      max-width: 100%;
      margin-top: .5em;
      input{
        padding-left: 1em;
      }
    }
  }

  .header-lang{
    &__item{
      width: 2.9375em;
      height: 1.625em;
    }
  }

  .burger{
    display: block;
    width: 1.125em;
    height: 1.125em;
    background-image: url(../img/burger.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 1.25em;
  }

  .menu{
    transition: all .5s;
    transform: translateX(-200%);
    opacity: 0;
    flex-direction: column;
    align-items: unset;
    position: fixed;
    top: 0;
    left: 0;
    width: 20.625em;
    height: 100vh;
    overflow-y: auto;
    max-width: calc(100% - 2.5em);
    background: #FFFFFF;
    box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.15);
    z-index: 11;
    padding: 1.5em 1.5em 4em;
    &__item{
      margin: 2.5em 0 0;
      &-hidden{
        padding: 1.25em 0 0 1.25em;
        display: none;
      }
    }
  }

  .submenu{
    .container{
      padding: 0;
    }
    &__inner{
      display: block;
    }
    &__left{
      padding: 0;
    }
    &__right{
      display: none;
    }
    &__title{
      display: none;
    }
    &__groups,
    &__links{
      margin: 0;
      column-count: 1;
    }
    &__services{
      grid-template-columns: 100%;
      gap: 0;
      margin: 0;
      .display4{
        display: none;
      }
      &-links{
        margin: 0;
      }
      &-link{
        margin-bottom: 1em;
      }
    }
  }

  .header-bottom{
    display: none;
  }
}
