@import "../vars.scss";
.breadcrumbs {
    padding: 4.125em 0 .625em;
    &__item {
        font-weight: 600;
        font-size: .625em;
        line-height: 150%;
        position: relative;
        display: inline;
        color: $black;
        text-transform: uppercase;
        &:last-child{
            color: $gray;
        }
        &:not(:first-child) {
            margin-left: 3.2em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1.2em;
                height: 1.2em;
                background-image: url(../img/bread-arrow.svg);
                top: -.1em;
                right: 100%;
                margin-right: 1.3em;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding-top: 2em;
    }
}
